import classNames from 'classnames/bind';
import { Link } from 'react-router-dom';
import { FavoriteIcon } from '../Icons';
import styles from './NoAlbum.module.scss';

const cx = classNames.bind(styles);

function NoAlbum() {
    return (
        <div className={cx('no-album')}>
            <div className={cx('no-album-icon')}>
                <FavoriteIcon />
            </div>
            <h1 className={cx('no-album-title')}>Bi ht bn yu thch s xut hin  y</h1>
            <p className={cx('no-album-des')}>Lu bi ht bng cch nhn vo biu tng tri tim.</p>
            <Link to="/search" className={cx('no-album-btn')}>
                Tm bi ht
            </Link>
        </div>
    );
}

export default NoAlbum;
