import ListMusic from '../../components/ListMusic';
import classNames from 'classnames/bind';
import styles from './Home.module.scss';
import songs, { data } from '../../components/Music/Music';

// console.log('data ', data);

const cx = classNames.bind(styles);

function Home() {
    return (
        <div className={cx('wrapper')}>
            <ListMusic listMusic={songs.album} title="Tm trng" />
            <ListMusic
                listMusic={songs.album}
                title="Hoi nim"
                subTitle="Nhng danh sch pht ton cc bn nhc yu thch, nghe mi khng chn."
            />

            <ListMusic listMusic={data.songs} title={data.title} subTitle={data.subTitle} />

            <ListMusic listMusic={songs.album} title="Tm trng" />
            <ListMusic listMusic={songs.album} title="Tm trng" />
        </div>
    );
}

export default Home;
